/**
 * Interface representing the options for handling errors.
 * 
 * @interface ErrorHandlerOptions
 * 
 * @property {string} message - The error message to be displayed.
 * @property {any} [requestOptions] - Optional request options associated with the error.
 * @property {number} statusCode - The HTTP status code associated with the error.
 * @property {string} name - The name of the error.
 * @property {Error} [error] - Optional original error object.
 */
export interface NyroErrorInterface {
    message: string;
    requestOptions?: any;
    statusCode: number;
    name: string;
    error?: Error;
};

/** 
* Interface representing the error response object.
* 
* @interface NyroErrorResponse
* 
* @property {string} message - The error message to be displayed.
* @property {any} [requestOptions] - Optional request options associated with the error.
* @property {string} [statusText] - The status text associated with the error.
* @property {number} statusCode - The HTTP status code associated with the error.
* @property {string} name - The name of the error.
* @property {Error} [error] - Optional original error object.
*/
export interface NyroErrorResponse {
    message: string;
    requestOptions?: any;
    statusText?: string;
    statusCode: number;
    name: string;
    error?: Error;
};


export function isNyroError(error: any): error is ErrorHandler {
    return error instanceof ErrorHandler;
};


export default class ErrorHandler extends Error {
    requestOptions?: any;
    statusText?: string;
    statusCode: number;
    constructor(errorHandlerOptions: NyroErrorInterface) {
        super(errorHandlerOptions.message);
        this.name = 'NyroError';
        this.statusText = StatusText[errorHandlerOptions.statusCode] || StatusText['unknown'];
        this.requestOptions = errorHandlerOptions.requestOptions;
        this.statusCode = errorHandlerOptions.statusCode;
        this.stack = errorHandlerOptions.error?.stack;
    }
}